import { useMemo, useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { JobDetailsContext } from '../../components/App';
import axios from 'axios';
import ClipLoader from 'react-spinners/ClipLoader';
import AdditionalInfo from 'components/AdditionalInfo/AdditionalInfo';
import JobDescription from 'components/JobDescription/JobDescription';
import Pagination from 'components/Pagination/Pagination';
import ModalForm from 'components/ModalForm/ModalForm';
import PageHeader from 'components/PageHeader/PageHeader';
import Button from 'react-bootstrap/Button';
import {
  ListItem,
  List,
  ContentWrapper,
  Loader,
  Circle,
  ButtonGroup,
  Error,
} from './jobBoard.styled';
import { getAbbreviation } from 'helpers/helpers';
import { BASE_URL } from 'constants/baseURL';

const JobBoard = () => {
  const [jobs, setJobs] = useState([]);
  const [page, setPage] = useState(1);
  const [error, setError] = useState(false);
  const [dataError, setDataError] = useState(false);
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [modalShow, setModalShow] = useState(false);
  const { category, setJobId } = useContext(JobDetailsContext);
  const categoryParam = category ? `category=${category}` : null;
  const navigate = useNavigate();

  useMemo(() => {
    setLoading(true);
    axios
      .get(`${BASE_URL}?${categoryParam}&page=${page}`)
      .then(res => {
        // console.log('res', res.data.results);
        if (res.data.results.length !== 0) {
          setJobs(res.data.results);
          setSuccess(true);
          setDataError(false);
          setLoading(false);
        } else {
          setDataError(true);
          setLoading(false);
        }
      })
      .catch(err => {
        console.error('Error:', err);
        setError(true);
      });
  }, [page, categoryParam]);

  const goToDetails = id => {
    setJobId(id);
    navigate('details');
  };

  // console.log('jobs', jobs);

  return (
    <>
      <PageHeader />
      {loading && (
        <Loader>
          <ClipLoader color="#3A4562" size={36} aria-label="Loading Spinner" />
        </Loader>
      )}
      {error && <Error>Oops... Something went wrong</Error>}
      {dataError ? (
        <Error>There is no data for your request. Please, try again!</Error>
      ) : (
        <>
          <List>
            {success &&
              jobs &&
              jobs.map(job => (
                <ListItem key={job.id} className="mb-2">
                  <ContentWrapper>
                    <Circle>{getAbbreviation(job.name)}</Circle>
                    <div>
                      <AdditionalInfo job={job} />
                      <JobDescription job={job} />
                    </div>
                  </ContentWrapper>
                  <ButtonGroup>
                    <Button
                      onClick={() => goToDetails(job.id)}
                      type="button"
                      variant="outline-secondary"
                      className="me-2"
                    >
                      Details
                    </Button>
                    <Button
                      onClick={() => setModalShow(true)}
                      type="button"
                      variant="outline-success"
                    >
                      Apply now
                    </Button>
                  </ButtonGroup>
                </ListItem>
              ))}
          </List>

          <Pagination setCurrentPage={setPage} />
          <ModalForm show={modalShow} onHide={() => setModalShow(false)} />
        </>
      )}
    </>
  );
};

export default JobBoard;
